@import './variables';
@import './libs/normalize';

@import './components/all';

body {
  background-color: $background;
  font-size: 16px;
}

.wrapper {
  position: relative;
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;
  @media (max-width: 767.99px) {
    width: 100%;
    margin: 0;
    max-width: none;
    border-radius: 0;
  }
}

.content {
  padding: 175px 0 80px 0;
  overflow: hidden;
  &__title {
    margin: 35px 0;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 30px;
    @media (max-width: 1445px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1090px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 480px) {
      grid-template-columns: 320px;
      justify-content: center;
    }
    @media (max-width: 375px) {
      grid-template-columns: 1fr;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 15px;
    @media (max-width: 424.99px) {
      margin-left: -30px;
    }
  }
  @media (max-width: 424.99px) {
    padding: 20px 0 0 0;
  }
  @media (max-width: 767.99px) {
    padding-top: 150px;
  }
  @media (max-width: 599.99px) {
    padding-top: 107px;
  }
  @media (max-width: 424.99px) {
    padding-top: 100px;
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 20px auto 90px auto;
    .content__title {
      margin: 0;
    }
  }
  @media (max-width: 767.99px) {
    width: 93%;
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

.count-item {
  &__container {
    width: 137px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

  .content__items {
    display: block;
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
    &-element {
      b {
        white-space: nowrap;
      }
    }
    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      flex: 0 1 9.75%;
      img {
        width: 80px;
        height: 80px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      // width: 40%;
      flex: 1 1 38%;
      margin-right: 15px;
      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      // width: 13%;
      flex: 1 0 13%;
      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }
        &:disabled {
          opacity: 0.5;
          pointer-events: none;
        }
      }

      b {
        font-size: 22px;
        margin: 0 10px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      // width: 33%;
      flex: 1 1 33%;

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      // width: 4%;
      flex: 1 1 4%;
      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
    @media (max-width: 679.99px) {
      flex-wrap: wrap;
      &-img, &-info, &-remove {
        margin-bottom: 15px;
      }
      &-img {
        flex: 0 1 13%;
        order: 1;
      }
      &-info {
        flex: 1 1 50%;
        margin-right: 0;
        order: 2;
      }
      &-count {
        order: 4;
        flex: 1 1 auto;
      }
      &-remove {
        order: 3;
        flex: 1 1 13%;
      }
      &-price {
        order: 5;
        flex: 0;
      }
    }
    // @media (max-width: 374.99px) {
    //   &-info {
    //     flex: 1 1 50%;
    //   }
    // }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }

      @media (max-width: 424.99px) {
        margin-top: 20px;
      }
    }
    &-fixed {
      display: none;
    }
    @media (max-width: 424.99px) {
      &-fixed {
        display: block;
        transform: translateY(100%);
        position: fixed;
        bottom: 0;
        left: 0;
        background: #fff;
        width: 100%;
        padding: 14px 14px;
        &.active {
          transform: translateY(0);
          transition: transform 0.2s ease-in-out;
        }
        .pay-btn {
          padding: 14px;
        }
      }
      margin: 20px 0 0 0;
      // position: fixed;
      // bottom: 0;
      // left: 0;
      // transform: translateY(100%);
      // background: #fff;
      // width: 100%;
      // padding: 14px 14px;
      // margin: 0;
    }
  }

  &--empty {
    margin: 0 auto;
    width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
  @media (max-width: 464.99px) {
    &__bottom {
      &-buttons {
        flex-wrap: wrap;
        row-gap: 10px;
        .button {
          width: 100%;
        }
      }
      &-details {
        span {
          font-size: 17px;
        }
      }
    }
    .content__title {
      font-size: 20px;
      svg {
        width: 20px;
        height: 20px;
      }
    }
    .cart__clear {
      span {
        font-size: 13px;
      }
    }
    .cart__item-img {
      img {
        width: 60px;
        height: 60px;
      }
    }
    .cart__item-info {
      h3 {
        font-size: 18px;
        line-height: 21px;
      }
      p {
        font-size: 15px;
      }
    }
  }
}

.notfound {
  text-align: center;
  padding-bottom: 100px;
  icon {
    font-size: 50px;
    margin-bottom: 10px;
    display: block;
  }
  h2 {
    font-size: 31px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  p {
    color: rgb(116 116 116);
    font-size: 17px;  
  }
}

.info {
  text-align: center;
  margin: 0 auto;
  max-width: 455px;
  &__image {
    border-radius: 50%;
    background-color: rgb(243, 241, 241);
    width: 130px;
    height: 130px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    img {
      width: 100px;
      height: 100px;
    }
  }
  &__title {
    font-weight: 700;
    margin-bottom: 5px;
  }
  &__text {
    color: rgb(139, 139, 139);
  }
}